___CSS_LOADER_EXPORT___.push([module.id, ".box[data-v-4ce73cb9]{background-color:#fff;border-radius:5px;box-shadow:0 .125rem .25rem rgba(0,0,0,.11);min-width:400px;padding:2rem}", "",{"version":3,"sources":["webpack://./resources/js/components/Form/Register.vue"],"names":[],"mappings":"AAsNA,sBAEI,qBAAuB,CAGvB,iBAAkB,CADlB,2CAAkD,CAHlD,eAAgB,CAEhB,YAGJ","sourcesContent":["<template>\n    <Master>\n        <div class=\"container\">\n            <div class=\"row my-5 d-flex justify-content-center\">\n                <div class=\"col-md-6\">\n                    <div class=\"box\">\n                        <h2 class=\"text-center\">Create Account</h2>\n                        <div class=\"m-3\">\n                            <p class=\"text-black-50 text-center mb-0\">\n                                Already have an account?\n                                <router-link\n                                    to=\"/login\"\n                                    class=\"text-decoration-none\"\n                                >\n                                    Sign in here\n                                </router-link>\n                            </p>\n                        </div>\n                        <div class=\"m-3\">\n                            <div class=\"row\">\n                                <div class=\"col-6\">\n                                    <button\n                                        class=\"btn btn-lg text-white w-100\"\n                                        @click=\"socialLogin('facebook')\"\n                                        style=\"background-color: #2374e1\"\n                                    >\n                                        <i class=\"fa-brands fa-facebook-f\"></i>\n                                        Facebook\n                                    </button>\n                                </div>\n                                <div class=\"col-6\">\n                                    <button\n                                        class=\"btn btn-lg btn-danger w-100\"\n                                        @click=\"socialLogin('google')\"\n                                    >\n                                        <i class=\"fa-brands fa-google\"></i>\n                                        Google\n                                    </button>\n                                </div>\n                            </div>\n                        </div>\n                        <hr class=\"text-black-50\" />\n                        <form @submit.prevent=\"register\">\n                            <div class=\"m-3\">\n                                <label for=\"name\" class=\"form-label\"\n                                    >Name</label\n                                >\n                                <input\n                                    type=\"text\"\n                                    id=\"name\"\n                                    class=\"form-control\"\n                                    placeholder=\"Your Name\"\n                                    v-model=\"name\"\n                                />\n                                <p v-if=\"errors.name\" class=\"text text-danger\">\n                                    {{ errors.name[0] }}\n                                </p>\n                            </div>\n                            <div class=\"m-3\">\n                                <label for=\"email\" class=\"form-label\"\n                                    >Email</label\n                                >\n                                <input\n                                    type=\"email\"\n                                    id=\"email\"\n                                    class=\"form-control\"\n                                    placeholder=\"E-mail\"\n                                    v-model=\"email\"\n                                />\n                                <p v-if=\"errors.email\" class=\"text text-danger\">\n                                    {{ errors.email[0] }}\n                                </p>\n                            </div>\n                            <div class=\"m-3\">\n                                <label for=\"password\" class=\"form-label\"\n                                    >Password</label\n                                >\n                                <input\n                                    type=\"password\"\n                                    id=\"password\"\n                                    class=\"form-control\"\n                                    placeholder=\"********\"\n                                    v-model=\"password\"\n                                />\n                                <p\n                                    v-if=\"errors.password\"\n                                    class=\"text text-danger\"\n                                >\n                                    {{ errors.password[0] }}\n                                </p>\n                            </div>\n                            <div class=\"m-3\">\n                                <label\n                                    for=\"profile\"\n                                    class=\"btn btn-outline-dark\"\n                                    ><i class=\"fa-solid fa-upload\"></i> Choose\n                                    Profile</label\n                                >\n                                <input\n                                    type=\"file\"\n                                    id=\"profile\"\n                                    class=\"d-none form-control\"\n                                    @change=\"setProfile\"\n                                />\n                                <p\n                                    v-if=\"errors.profile\"\n                                    class=\"text text-danger\"\n                                >\n                                    {{ errors.profile[0] }}\n                                </p>\n                            </div>\n\n                            <div\n                                class=\"m-3 d-flex justify-content-between align-items-center\"\n                            >\n                                <div class=\"form-check\">\n                                    <input\n                                        class=\"form-check-input\"\n                                        type=\"checkbox\"\n                                        value=\"\"\n                                        id=\"remember\"\n                                        required\n                                    />\n                                    <label\n                                        class=\"form-check-label text-black-50\"\n                                        for=\"remember\"\n                                    >\n                                        I accept the\n                                        <a href=\"\" class=\"text-decoration-none\"\n                                            >Terms and Conditions</a\n                                        >\n                                    </label>\n                                </div>\n                                <button\n                                    class=\"btn btn-primary text-white me-3\"\n                                    type=\"submit\"\n                                    :disabled=\"loading\"\n                                >\n                                    Register\n                                </button>\n                            </div>\n                        </form>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </Master>\n</template>\n<script>\nimport axios from \"axios\";\nimport Master from \"../layouts/Master\";\nexport default {\n    components: { Master },\n    data() {\n        return {\n            name: \"\",\n            email: \"\",\n            password: \"\",\n            profile: \"\",\n            loading: false,\n            errors: {},\n        };\n    },\n    created(){\n        this.$Progress.start();\n    },\n    mounted(){\n        this.$Progress.finish();\n    },\n    methods: {\n        setProfile(e) {\n            this.profile = e.target.files[0];\n        },\n        async register() {\n            this.loading = true;\n            const formData = new FormData();\n            formData.append(\"name\", this.name);\n            formData.append(\"email\", this.email);\n            formData.append(\"password\", this.password);\n            formData.append(\"profile\", this.profile);\n\n            const res = await axios.post(\"/api/register\", formData);\n            console.log(res.data);\n            const { success, data } = res.data;\n            if (success === true) {\n                localStorage.setItem(\"auth\", JSON.stringify(data));\n                this.$store.dispatch(\"getAuth\", data);\n                this.loading = false;\n                this.$router.push({\n                    name: \"verify\",\n                });\n            } else {\n                this.errors = data;\n                localStorage.clear();\n                this.loading = false;\n            }\n        },\n        socialLogin(provider) {\n            this.$Progress.start();\n            this.$store.dispatch(\"socialLogin\", provider).then((resp) => {\n                if (resp.data.url) {\n                    this.$Progress.finish();\n                    window.location.href = resp.data.url;\n                }\n                console.log(resp);\n            });\n        },\n        cancel() {\n            return false;\n        },\n    },\n};\n</script>\n<style scoped>\n.box {\n    min-width: 400px;\n    background-color: white;\n    padding: 2rem;\n    box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.11);\n    border-radius: 5px;\n}\n</style>\n"],"sourceRoot":""}]);
